import React from "react";
import { SketchPicker } from "react-color";

export default class extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      bodyBg: '#000'
    }
  }

  render() {
    const { bodyBg } = this.state;
    return (
      <div className="customize">
        <div
          className="picker"
          style={{
            backgroundColor: bodyBg
          }}
        />
      </div>
    );
  }
}
